import { ChangeDetectionStrategy, Component, Input, OnDestroy, OnInit } from "@angular/core";

import { Store } from "@ngrx/store";
import { Observable, Subscription } from "rxjs";

import * as fromRoot from "../../reducers";
import * as reader from "../../actions/reader";
import * as bookPosition from "../../actions/book-position";

import { IntervalService } from "../../services";

import { Book, BookPosition } from "../../models";

@Component({
	selector: "app-rsv-view",
	templateUrl: "./rsv-view.component.template.html"
})
export class RsvViewComponent implements OnInit, OnDestroy {
	@Input()
	word: string;

	@Input()
	book: Book;

	currentWordIdx: number = 0;

	get phrase(): string[] {
		if (this.word == null) {
			return [];
		}
		if (this.phraseRequiresSplit) {
			const phraseArr = this.word.split(" ");
			return phraseArr.slice(0, phraseArr.length - 1);
		}
		return this.word.split(" ");
	}

	get phraseRequiresSplit(): boolean {
		return this.word.length > 15 && this.word.split(" ").length > 1;
	}

	// STORE ITEMS
	private bookPosition$: Observable<BookPosition>;
	private play$: Observable<boolean>;
	private phraseLength$: Observable<number>;
	private wpm$: Observable<number>;

	// SUBSCRIPTIONS
	private bookPositionSubscription: Subscription;
	private playSubscription: Subscription;
	private intervalSubscription: Subscription;

	constructor(
		private intervalService: IntervalService,
		private store: Store<fromRoot.State>
	) {
		this.bookPosition$ = this.store.select(fromRoot.getBookPosition);
		this.play$ = this.store.select(fromRoot.getPlay);
		this.wpm$ = this.store.select(fromRoot.getWpm);
		this.phraseLength$ = this.store.select(fromRoot.getPhraseLength);
	}

	ngOnInit(): void {
		this.playSubscription =
			Observable
				.combineLatest(this.play$, this.wpm$, this.phraseLength$)
				.debounceTime(100)
				.subscribe(([play, wpm, phraseLength] ) => {
					this.cancelSubscription(this.intervalSubscription);
					if (play) {
						this.startRsvInterval(wpm, phraseLength);
					}
				});

		this.bookPositionSubscription =
			this.bookPosition$.subscribe((position: BookPosition) => {
				if (bookPosition == null || this.book == null) {
					return;
				}

				this.currentWordIdx = this.book.content[position.paragraphIdx].firstIndex + position.wordIdx;
			});
	}

	ngOnDestroy(): void {
		this.cancelSubscription(this.intervalSubscription);
		this.cancelSubscription(this.playSubscription);
	}

	private startRsvInterval(wpm: number, phraseLength: number): void {
		this.cancelSubscription(this.intervalSubscription);

		this.intervalSubscription =
			this.intervalService
				.getIntervalFromWpm(wpm, phraseLength)
				.subscribe(() => {
					// if the phrase was split, step one fewer words than usual
					this.phraseRequiresSplit ?
						this.store.dispatch(new bookPosition.StepAction(phraseLength - 1)) :
						this.store.dispatch(new bookPosition.StepAction(phraseLength));
				});
	}

	private cancelSubscription(sub: Subscription): void {
		if (sub == null) {
			return;
		}
		sub.unsubscribe();
		sub = null;
	}
}



